.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.header {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 40px 0 40px;
    width: 100%;
    height: 60px;
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
    z-index: 500;
    display: flex;
    align-items: center;
    & > a {
        margin: 10px;
        font-size: 18px;
        cursor: pointer;
    }
}
.content {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 60px;
    bottom: 100px;
    width: 100%;
    height: 100%;
}
.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #ffffff;
}
